import { useState } from "react";
import { EduMgmtimg, Button } from "./styled-component";
import { Container } from "react-bootstrap";
import BusinesSarea from "./Contents/Businex/BusinesSarea";
import Eduservices from "./Contents/Eduservices/Eduservices";

const EduMgmt = () => {
  const [activeButton, setActiveButton] = useState(1);

  const handleClick = (e) => setActiveButton(e);

  return (
    <section>
      <Container>
        <EduMgmtimg>
          <div>
            <h2>   </h2>
                   .
            <br />
               !
          </div>
        </EduMgmtimg>
        <Button clicked={activeButton === 2} onClick={() => handleClick(1)}>
           
        </Button>
        <Button clicked={activeButton === 1} onClick={() => handleClick(2)}>
           
        </Button>
        {activeButton === 1 ? <BusinesSarea /> : <Eduservices />}
      </Container>
    </section>
  );
};

export default EduMgmt;
